*{
  margin:0;
  padding:0;
  box-sizing: border-box ;
}
body{
  font-size: 16px;
  /* background-color: #191c21; */


}
.App{
  min-height: 100vh;
  background-color: #191c21;
  display:flex;
  flex-direction: column;
}

.App-header {
  background-color: #191c21;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  color: white;
  width: 100%;
 
}
.App-header h3{
color:rgb(82, 85, 28);
}
.buttons{
  margin:25px 0 20px 0
}
.buttons a{
  border:1px solid white;
  border-radius:.5rem;
  padding:.5rem ;
  margin-right:10px;
  background-color: rgb(233, 233, 228);
  color:#111;
  text-decoration: none;
  cursor:pointer;
 display:inline-block;
 min-width:150px;
 text-align: center;
}
.buttons a i{

  padding-right:.5rem;
  

}
.menuIcon{
   width: 200px;
}


.about{
  
  min-height: 20vh;
  color:#fff;
  padding:10px;
  text-align: justify;
}
.mainContainer{
  background-color: #282c34;

  padding: 3.5rem 1.9rem;
  
  background-image: url(./assets/react-icon-large.png);
  background-repeat: no-repeat;
  background-position: right 75%;
  width: 100%;

}
.mainContainer h1{
 color:#fff;
 font-size: 2.5rem;
  letter-spacing: -0.05rem;
  text-align: justify;
 
}
.mainContainer ul{
  margin-top: 3rem;
}
.mainContainer li{
  color:#fff;
  line-height: 1.2rem;
  padding-block: 10px;
  text-align: justify;
}
.mainContainer li::marker{
  color:#61dafb;
  font-size: 1.3rem;
}
.footer{
	background: #000;

	color:#fff;
	text-align: center;
  margin-top:auto;

 }
 
.rrss a{
 
	text-decoration: none;
	color:rgb(123, 119, 119);
	font-size:30px;
  margin-left:30px;
	
}
footer .rrss a [title]{
	
	color:#fff;
	background:none;
	
	
}
/* footer section */

.copyright .name{
	color:rgb(123, 119, 119);
	font-size: 20px;
}
.copyright .far{
  font-size:15px;
}
.copyright a{
  text-decoration:none;
}


